<script>
import axios from "axios"
import moment from 'moment'
import config from '../config'
export default {
  name: 'RedisInfo',
  props: {
    redis_ip: String,
    info_data: Object,
    logs_loading: Boolean,
    clients_loading: Boolean,
    logs_data: Array,
    clients_data: Array
  },
  data() {
    return {
      url: config.base_url,
      moment: moment,
      showConfig: false,
      configData: [],
      logs_columns: [{
        title: 'ID',
        dataIndex: 'id',
        width: 100,
      }, {
        title: '记录时间',
        dataIndex: 'time',
        width: 200,
        scopedSlots: { customRender: 'time' },
      }, {
        title: '执行时间(微秒)',
        dataIndex: 'time_used',
        width: 150,
      }, {
        title: '日志详情',
        dataIndex: 'msg',
      }],
      clients_columns: [{
        title: 'ID',
        dataIndex: 'id',
        width: 150,
      }, {
        title: '主机',
        dataIndex: 'addr',
        width: 200,
      }, {
        title: '连接时长',
        dataIndex: 'age',
        scopedSlots: { customRender: 'time' },
        width: 150,
      }, {
        title: '数据库ID',
        dataIndex: 'db',
        width: 150,
      }, {
        title: '订阅频道',
        dataIndex: 'sub',
        width: 150,
      }, {
        title: '最近执行',
        dataIndex: 'cmd',
        width: 150,
      }],
    }
  },
  methods: {
    get_config() {
      this.showConfig = true
      this.configData = []
      let comm = 'config get *'
      axios.get(this.url + `/containers?method=execute&ip=${this.redis_ip}&command=${comm}`)
        .then(result => {
          let code = result.data.code;
          if (code == 0) {
            let output = result.data.data
            for (let i = 0; i < output.length; i += 2) {
              if (output[i] === 'client-output-buffer-limit') {
                let buffers = output[i + 1].split(' ')
                this.configData.push({'key': `${output[i]}-${buffers[0]}`, 'value': `${buffers[1]} ${buffers[2]} ${buffers[3]}`})
                this.configData.push({'key': `${output[i]}-${buffers[4]}`, 'value': `${buffers[5]} ${buffers[6]} ${buffers[7]}`})
                this.configData.push({'key': `${output[i]}-${buffers[8]}`, 'value': `${buffers[9]} ${buffers[10]} ${buffers[11]}`})
              } else {
                this.configData.push({'key': output[i], 'value': output[i + 1]})
              }
            }
          }
        })
    },
    format_db_nums(str) {
      if (str === undefined) return 0
      if (str === "") return 0
      let s = str.split(',')[0]
      return s.split('=')[1]
    },
    formatSeconds(value) {
      var secondTime = parseInt(value);// 秒
      var minuteTime = 0;// 分
      var hourTime = 0;// 小时
      if(secondTime > 60) {//如果秒数大于60，将秒数转换成整数
        //获取分钟，除以60取整数，得到整数分钟
        minuteTime = parseInt(secondTime / 60);
        //获取秒数，秒数取佘，得到整数秒数
        secondTime = parseInt(secondTime % 60);
        //如果分钟大于60，将分钟转换成小时
        if(minuteTime > 60) {
          //获取小时，获取分钟除以60，得到整数小时
          hourTime = parseInt(minuteTime / 60);
          //获取小时后取佘的分，获取分钟除以60取佘的分
          minuteTime = parseInt(minuteTime % 60);
        }
      }
      var result = "" + parseInt(secondTime) + "秒";

      if(minuteTime > 0) {
        result = "" + parseInt(minuteTime) + "分" + result;
      }
      if(hourTime > 0) {
        result = "" + parseInt(hourTime) + "小时" + result;
      }
      return result;
    },
  }
}
</script>

